import React from "react";
import "../App.css";

export default function Cards(props) {
  const { small_image, title, authorname, posted_on, description } = props;
  return (
    <div className="card">
      <div className="image-wrapper">
        <img className="image" src={small_image} />
      </div>
      <div className="text-wrapper">
        <h1>{title}</h1>
        <h3>
          {authorname}
          <span> | {posted_on.split("T")[0]}</span>
        </h3>
        <p>{description.slice(0, 100)}...</p>
      </div>
    </div>
  );
}
